// ==============================
// Main Imports
// ========================================

import Vue from 'vue'
import vueHeadful from 'vue-headful'
import App from './App.vue'
import router from './router'

// ==============================
// Prismic Imports
// ========================================

import PrismicVue from 'prismic-vue'
import linkResolver from './link-resolver'
import accessToken from './prismic-token'

// ==============================
// Components
// ========================================

// Import component scripts
import './components/_components.js'

// Vue Imports
import Icons from './components/icons'
import Decor from './components/decor'

// Icons Registration
Icons.forEach(icon => { Vue.component(icon.name, icon) })

// Decor Registration
Decor.forEach(decor => { Vue.component(decor.name, decor) })

// Vue Headful Registration
Vue.component('vue-headful', vueHeadful)

// ==============================
// Plugins
// ========================================

// Prismic Plugin Initialisation
Vue.use(PrismicVue, {
  endpoint: 'https://rhysmatthew.prismic.io/api/v2',
  linkResolver,
  apiOptions: { accessToken },
  preview: false
})

// ==============================
// Vue Instance
// ========================================

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
