<template>
  <div ref="decorContainer" class="decor"></div>
</template>

<script>
import * as THREE from 'three'
import throttle from 'lodash/throttle'

export default {
  name: 'Decor',
  props: {
    classes: {
      type: String,
      default: ''
    }
  },

  mounted () {
    this.handleThreeJS()
  },

  methods: {
    handleThreeJS () {
      const camera = new THREE.PerspectiveCamera(55, 1, 2, 1000)

      const scene = new THREE.Scene()

      const geometry = new THREE.IcosahedronGeometry(350, 2)
      const material = new THREE.MeshBasicMaterial({ color: 0xdcdcdc, wireframe: true, wireframeLineWidth: 4 })

      const mesh = new THREE.Mesh(geometry, material)
      scene.add(mesh)

      const renderer = new THREE.WebGLRenderer({ alpha: true })
      if (window.innerWidth < 480) {
        renderer.setSize((window.innerWidth * 2), (window.innerHeight * 1.5))
        camera.position.z = 500
        camera.position.y = -320
        camera.position.x = -150
      } else if (window.innerWidth > 480 && window.innerWidth < 768) {
        renderer.setSize((window.innerWidth * 2), (window.innerHeight * 1.5))
        camera.position.z = 500
        camera.position.y = -400
        camera.position.x = -100
      } else {
        renderer.setSize(window.innerWidth, (window.innerHeight * 2.25))
        camera.position.z = 500
        camera.position.y = -400
        camera.position.x = -60
      }
      renderer.setClearColor(0xffffff, 0)

      this.$refs.decorContainer.appendChild(renderer.domElement)

      animate()

      function animate () {
        requestAnimationFrame(animate)
        mesh.rotation.x += 0.0003
        mesh.rotation.y += 0.0003
        renderer.render(scene, camera)
      }

      window.addEventListener('resize', throttle(function () {
        if (window.innerWidth < 1024) {
          renderer.setSize((window.innerWidth * 2), (window.innerHeight * 1.5))
          camera.position.z = 500
          camera.position.y = -400
          camera.position.x = -100
        } else {
          renderer.setSize(window.innerWidth, (window.innerHeight * 2.25))
          camera.position.z = 500
          camera.position.y = -400
          camera.position.x = -60
        }
      }, 250, { leading: true, trailing: true }))
    }
  }
}
</script>
